/**
 * ============================================================================
 * CONTEXTE D'AUTHENTIFICATION - PedaClic
 * ============================================================================
 * Gestion centralise de l'authentification Firebase
 * Fournit l'tat utilisateur et les fonctions auth  toute l'application
 * 
 * @author PedaClic Team
 * @version 1.0.0
 */

import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import {
  onAuthStateChanged,
  signInWithEmailAndPassword,
  createUserWithEmailAndPassword,
  signOut,
  updateProfile as updateAuthProfile,
  User as FirebaseUser
} from 'firebase/auth';
import { doc, getDoc, setDoc, updateDoc, serverTimestamp } from 'firebase/firestore';
import { auth, db } from '../firebase';
import type { User, UserRole, RegisterFormData, AuthContextType } from '../index';

// ==================== MESSAGES D'ERREUR EN FRANAIS ====================

const ERROR_MESSAGES: Record<string, string> = {
  'auth/email-already-in-use': 'Cette adresse email est dj utilise.',
  'auth/invalid-email': 'Adresse email invalide.',
  'auth/operation-not-allowed': 'Opration non autorise.',
  'auth/weak-password': 'Le mot de passe doit contenir au moins 6 caractres.',
  'auth/user-disabled': 'Ce compte a t dsactiv.',
  'auth/user-not-found': 'Aucun compte associ  cette adresse email.',
  'auth/wrong-password': 'Mot de passe incorrect.',
  'auth/too-many-requests': 'Trop de tentatives. Veuillez ressayer plus tard.',
  'auth/network-request-failed': 'Erreur de connexion. Vrifiez votre connexion internet.',
  'default': 'Une erreur est survenue. Veuillez ressayer.'
};

/**
 * Traduit le code d'erreur Firebase en message franais
 * @param errorCode - Code d'erreur Firebase
 * @returns Message d'erreur en franais
 */
const getErrorMessage = (errorCode: string): string => {
  return ERROR_MESSAGES[errorCode] || ERROR_MESSAGES['default'];
};

// ==================== CONTEXTE ====================

/** Contexte d'authentification avec valeur par dfaut null */
const AuthContext = createContext<AuthContextType | null>(null);

// ==================== PROVIDER ====================

interface AuthProviderProps {
  children: ReactNode;
}

/**
 * Provider d'authentification
 * Enveloppe l'application et fournit l'tat d'authentification
 */
export const AuthProvider: React.FC<AuthProviderProps> = ({ children }) => {
  // ==================== TAT ====================

  const [currentUser, setCurrentUser] = useState<User | null>(null);
  const [loading, setLoading] = useState<boolean>(true);

  // ==================== FONCTIONS UTILITAIRES ====================

  /**
   * Rcupre les donnes utilisateur depuis Firestore
   * @param uid - ID utilisateur Firebase
   * @returns Donnes utilisateur ou null
   */
  const fetchUserData = async (uid: string): Promise<User | null> => {
    try {
      const userDoc = await getDoc(doc(db, 'users', uid));
      
      if (userDoc.exists()) {
        const data = userDoc.data();
        return {
          uid,
          email: data.email,
          displayName: data.displayName,
          role: data.role as UserRole,
          isPremium: data.isPremium || false,
          subscriptionEnd: data.subscriptionEnd?.toDate() || null,
          photoURL: data.photoURL,
          createdAt: data.createdAt?.toDate() || new Date(),
          lastLogin: data.lastLogin?.toDate()
        };
      }
      return null;
    } catch (error) {
      console.error('Erreur lors de la rcupration des donnes utilisateur:', error);
      return null;
    }
  };

  /**
   * Cre ou met  jour le document utilisateur dans Firestore
   * @param user - Utilisateur Firebase
   * @param additionalData - Donnes supplmentaires optionnelles
   */
  const createOrUpdateUserDoc = async (
    user: FirebaseUser,
    additionalData?: Partial<User>
  ): Promise<void> => {
    const userRef = doc(db, 'users', user.uid);
    const userDoc = await getDoc(userRef);

    if (!userDoc.exists()) {
      // Cration du document utilisateur
      await setDoc(userRef, {
        email: user.email,
        displayName: user.displayName || additionalData?.displayName || '',
        role: additionalData?.role || 'eleve',
        isPremium: false,
        photoURL: user.photoURL,
        createdAt: serverTimestamp(),
        lastLogin: serverTimestamp()
      });
    } else {
      // Mise  jour de la dernire connexion
      await updateDoc(userRef, {
        lastLogin: serverTimestamp()
      });
    }
  };

  // ==================== FONCTIONS D'AUTHENTIFICATION ====================

  /**
   * Connexion avec email et mot de passe
   * @param email - Adresse email
   * @param password - Mot de passe
   */
  const login = async (email: string, password: string): Promise<void> => {
    try {
      const userCredential = await signInWithEmailAndPassword(auth, email, password);
      await createOrUpdateUserDoc(userCredential.user);
    } catch (error: any) {
      throw new Error(getErrorMessage(error.code));
    }
  };

  /**
   * Inscription d'un nouvel utilisateur
   * @param data - Donnes du formulaire d'inscription
   */
  const register = async (data: RegisterFormData): Promise<void> => {
    try {
      // Vrification des mots de passe
      if (data.password !== data.confirmPassword) {
        throw new Error('Les mots de passe ne correspondent pas.');
      }

      // Cration du compte Firebase Auth
      const userCredential = await createUserWithEmailAndPassword(
        auth,
        data.email,
        data.password
      );

      // Mise  jour du profil Firebase Auth
      await updateAuthProfile(userCredential.user, {
        displayName: data.displayName
      });

      // Cration du document utilisateur Firestore
      await createOrUpdateUserDoc(userCredential.user, {
        displayName: data.displayName,
        role: data.role
      });
    } catch (error: any) {
      if (error.message) {
        throw error;
      }
      throw new Error(getErrorMessage(error.code));
    }
  };

  /**
   * Dconnexion de l'utilisateur
   */
  const logout = async (): Promise<void> => {
    try {
      await signOut(auth);
      setCurrentUser(null);
    } catch (error: any) {
      throw new Error('Erreur lors de la dconnexion.');
    }
  };

  /**
   * Met  jour le profil utilisateur
   * @param data - Donnes  mettre  jour
   */
  const updateProfile = async (data: Partial<User>): Promise<void> => {
    if (!currentUser) {
      throw new Error('Aucun utilisateur connect.');
    }

    try {
      const userRef = doc(db, 'users', currentUser.uid);
      await updateDoc(userRef, {
        ...data,
        updatedAt: serverTimestamp()
      });

      // Mise  jour de l'tat local
      setCurrentUser(prev => prev ? { ...prev, ...data } : null);
    } catch (error) {
      throw new Error('Erreur lors de la mise  jour du profil.');
    }
  };

  // ==================== EFFET D'COUTE AUTH ====================

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async (firebaseUser) => {
      if (firebaseUser) {
        // Utilisateur connect - rcuprer ses donnes
        const userData = await fetchUserData(firebaseUser.uid);
        setCurrentUser(userData);
      } else {
        // Utilisateur dconnect
        setCurrentUser(null);
      }
      setLoading(false);
    });

    // Nettoyage de l'couteur
    return () => unsubscribe();
  }, []);

  // ==================== VALEUR DU CONTEXTE ====================

  const value: AuthContextType = {
    currentUser,
    loading,
    login,
    register,
    logout,
    updateProfile
  };

  // ==================== RENDU ====================

  return (
    <AuthContext.Provider value={value}>
      {/* Afficher un loader pendant le chargement initial */}
      {loading ? (
        <div className="loading-container">
          <div className="spinner"></div>
          <p>Chargement...</p>
        </div>
      ) : (
        children
      )}
    </AuthContext.Provider>
  );
};

// ==================== HOOK PERSONNALIS ====================

/**
 * Hook pour accder au contexte d'authentification
 * @returns Contexte d'authentification
 * @throws Error si utilis hors du Provider
 */
export const useAuth = (): AuthContextType => {
  const context = useContext(AuthContext);
  
  if (!context) {
    throw new Error('useAuth doit tre utilis  l\'intrieur d\'un AuthProvider');
  }
  
  return context;
};

// ==================== COMPOSANTS DE PROTECTION ====================

interface ProtectedRouteProps {
  children: ReactNode;
  allowedRoles?: UserRole[];
  requirePremium?: boolean;
}

/**
 * Composant de protection des routes
 * Vrifie l'authentification et les autorisations
 */
export const ProtectedRoute: React.FC<ProtectedRouteProps> = ({
  children,
  allowedRoles,
  requirePremium = false
}) => {
  const { currentUser, loading } = useAuth();

  // Afficher un loader pendant la vrification
  if (loading) {
    return (
      <div className="loading-container">
        <div className="spinner"></div>
        <p>Vrification des autorisations...</p>
      </div>
    );
  }

  // Redirection si non connect
  if (!currentUser) {
    return (
      <div className="error-container">
        <h2>Accs refus</h2>
        <p>Vous devez tre connect pour accder  cette page.</p>
        <a href="/connexion" className="btn btn-primary">Se connecter</a>
      </div>
    );
  }

  // Vrification des rles autoriss
  if (allowedRoles && !allowedRoles.includes(currentUser.role)) {
    return (
      <div className="error-container">
        <h2>Accs non autoris</h2>
        <p>Vous n'avez pas les permissions ncessaires pour accder  cette page.</p>
        <a href="/" className="btn btn-primary">Retour  l'accueil</a>
      </div>
    );
  }

  // Vrification du statut Premium
  if (requirePremium && !currentUser.isPremium) {
    return (
      <div className="error-container premium-required">
        <h2>Contenu Premium</h2>
        <p>Cette ressource est rserve aux abonns Premium.</p>
        <div className="premium-cta">
          <p><strong>2 000 FCFA/mois</strong> ou <strong>20 000 FCFA/an</strong></p>
          <a href="/premium" className="btn btn-premium">Devenir Premium</a>
        </div>
      </div>
    );
  }

  // Accs autoris
  return <>{children}</>;
};

/**
 * Composant de protection Admin
 * Raccourci pour les routes rserves aux administrateurs
 */
export const AdminRoute: React.FC<{ children: ReactNode }> = ({ children }) => (
  <ProtectedRoute allowedRoles={['admin']}>
    {children}
  </ProtectedRoute>
);

/**
 * Composant de protection Professeur
 * Raccourci pour les routes rserves aux professeurs et admins
 */
export const ProfRoute: React.FC<{ children: ReactNode }> = ({ children }) => (
  <ProtectedRoute allowedRoles={['admin', 'prof']}>
    {children}
  </ProtectedRoute>
);

export default AuthContext;
