/**
 * PAGE PREMIUM - PedaClic
 * Prsentation des avantages Premium et intgration paiement PayTech
 * Tarifs : 2 000 FCFA/mois ou 20 000 FCFA/an
 */

import React, { useState } from 'react';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { useAuth } from '../../hooks/useAuth';
import { doc, updateDoc, addDoc, collection, serverTimestamp } from 'firebase/firestore';
import { db } from '../../firebase';
import './PremiumPage.css';

/**
 * Types de plans d'abonnement
 */
type PlanType = 'mensuel' | 'annuel';

/**
 * Interface pour les plans
 */
interface Plan {
  id: PlanType;
  nom: string;
  prix: number;
  prixOriginal?: number;
  duree: string;
  economie?: string;
  popular?: boolean;
}

/**
 * Configuration des plans Premium
 */
const PLANS: Plan[] = [
  {
    id: 'mensuel',
    nom: 'Mensuel',
    prix: 2000,
    duree: '1 mois',
  },
  {
    id: 'annuel',
    nom: 'Annuel',
    prix: 20000,
    prixOriginal: 24000,
    duree: '12 mois',
    economie: '4 000 FCFA',
    popular: true,
  },
];

/**
 * Liste des avantages Premium
 */
const AVANTAGES = [
  {
    icone: '',
    titre: 'Accs illimit',
    description: 'Tous les cours de la 6me  la Terminale, sans restriction.',
  },
  {
    icone: '',
    titre: 'Vidos explicatives',
    description: 'Des cours en vido par des professeurs expriments.',
  },
  {
    icone: '',
    titre: 'Exercices corrigs',
    description: 'Des milliers d\'exercices avec corrections dtailles.',
  },
  {
    icone: '',
    titre: 'Suivi de progression',
    description: 'Tableau de bord personnel pour suivre vos rsultats.',
  },
  {
    icone: '',
    titre: 'Quiz interactifs',
    description: 'Testez vos connaissances avec des quiz par matire.',
  },
  {
    icone: '',
    titre: 'Support prioritaire',
    description: 'Assistance rapide pour toutes vos questions.',
  },
  {
    icone: '',
    titre: 'Accs mobile',
    description: 'Apprenez partout, sur tlphone ou tablette.',
  },
  {
    icone: '',
    titre: 'Mises  jour',
    description: 'Nouveaux contenus ajouts rgulirement.',
  },
];

/**
 * FAQ Premium
 */
const FAQ = [
  {
    question: 'Comment fonctionne l\'abonnement Premium ?',
    reponse: 'Une fois votre paiement effectu via Wave, Orange Money ou carte bancaire, votre compte est instantanment mis  niveau. Vous avez accs  tous les contenus Premium pendant la dure de votre abonnement.',
  },
  {
    question: 'Puis-je annuler mon abonnement ?',
    reponse: 'Oui, vous pouvez annuler  tout moment depuis votre espace personnel. Vous conservez l\'accs Premium jusqu\' la fin de la priode paye.',
  },
  {
    question: 'L\'abonnement se renouvelle-t-il automatiquement ?',
    reponse: 'Non, chez PedaClic, il n\'y a pas de renouvellement automatique. Vous dcidez quand renouveler votre abonnement.',
  },
  {
    question: 'Quels moyens de paiement acceptez-vous ?',
    reponse: 'Nous acceptons Wave, Orange Money, Free Money et les cartes bancaires (Visa, Mastercard) via notre partenaire PayTech.',
  },
  {
    question: 'Puis-je partager mon compte Premium ?',
    reponse: 'Chaque compte Premium est personnel et li  une adresse email. Le partage de compte est interdit pour garantir un suivi de progression personnalis.',
  },
];

const PremiumPage: React.FC = () => {
  // ==================== HOOKS ====================
  const { currentUser } = useAuth();
  const location = useLocation();
  const navigate = useNavigate();

  // ==================== TATS ====================
  const [selectedPlan, setSelectedPlan] = useState<PlanType>('annuel');
  const [loading, setLoading] = useState<boolean>(false);
  const [error, setError] = useState<string | null>(null);
  const [openFaq, setOpenFaq] = useState<number | null>(null);

  // Page de retour aprs connexion
  const returnTo = (location.state as { from?: string })?.from || '/';

  // ==================== VRIFICATION PREMIUM ====================
  
  /**
   * Vrifier si l'utilisateur est dj Premium
   */
  const isAlreadyPremium = (): boolean => {
    if (!currentUser) return false;
    if (!currentUser.isPremium) return false;
    
    // Vrifier si l'abonnement n'est pas expir
    if (currentUser.subscriptionEnd) {
      const endDate = currentUser.subscriptionEnd instanceof Date 
        ? currentUser.subscriptionEnd 
        : new Date(currentUser.subscriptionEnd);
      return endDate > new Date();
    }
    
    return true;
  };

  // ==================== HANDLERS ====================

  /**
   * Initialiser le paiement PayTech
   */
  const handlePayment = async () => {
    // Vrifier l'authentification
    if (!currentUser) {
      navigate('/connexion', { state: { from: '/premium' } });
      return;
    }

    // Vrifier si dj Premium
    if (isAlreadyPremium()) {
      setError('Vous tes dj abonn Premium !');
      return;
    }

    try {
      setLoading(true);
      setError(null);

      const plan = PLANS.find(p => p.id === selectedPlan);
      if (!plan) {
        throw new Error('Plan invalide');
      }

      // Crer une transaction en attente dans Firestore
      const transactionRef = await addDoc(collection(db, 'transactions'), {
        userId: currentUser.uid,
        userEmail: currentUser.email,
        planType: selectedPlan,
        montant: plan.prix,
        devise: 'XOF',
        statut: 'pending',
        dateTransaction: serverTimestamp(),
        createdAt: serverTimestamp(),
      });

      // Configuration PayTech
      const paytechConfig = {
        // Cl API PayTech ( remplacer par votre cl)
        apiKey: import.meta.env.VITE_PAYTECH_API_KEY || 'demo_api_key',
        apiSecret: import.meta.env.VITE_PAYTECH_API_SECRET || 'demo_api_secret',
        
        // Informations transaction
        item_name: `PedaClic Premium ${plan.nom}`,
        item_price: plan.prix,
        currency: 'XOF',
        ref_command: transactionRef.id,
        
        // URLs de callback
        success_url: `${window.location.origin}/premium/success?ref=${transactionRef.id}`,
        cancel_url: `${window.location.origin}/premium/cancel?ref=${transactionRef.id}`,
        ipn_url: `${import.meta.env.VITE_API_URL || ''}/api/paytech/ipn`,
        
        // Informations client
        custom_field: JSON.stringify({
          userId: currentUser.uid,
          planType: selectedPlan,
          transactionId: transactionRef.id,
        }),
        
        // Mode (test ou live)
        env: import.meta.env.VITE_PAYTECH_ENV || 'test',
      };

      // Appel  l'API PayTech pour obtenir l'URL de paiement
      // Note: En production, cet appel devrait tre fait ct serveur
      const response = await fetch('https://paytech.sn/api/payment/request-payment', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'API_KEY': paytechConfig.apiKey,
          'API_SECRET': paytechConfig.apiSecret,
        },
        body: JSON.stringify({
          item_name: paytechConfig.item_name,
          item_price: paytechConfig.item_price,
          currency: paytechConfig.currency,
          ref_command: paytechConfig.ref_command,
          command_name: `Abonnement Premium ${plan.nom} - ${currentUser.email}`,
          env: paytechConfig.env,
          ipn_url: paytechConfig.ipn_url,
          success_url: paytechConfig.success_url,
          cancel_url: paytechConfig.cancel_url,
          custom_field: paytechConfig.custom_field,
        }),
      });

      const data = await response.json();

      if (data.success === 1 && data.redirect_url) {
        // Rediriger vers la page de paiement PayTech
        window.location.href = data.redirect_url;
      } else {
        // Mode dmo - simuler le succs
        console.log('Mode dmo PayTech - Simulation de paiement');
        
        // Calculer la date d'expiration
        const now = new Date();
        const expirationDate = new Date(now);
        if (selectedPlan === 'mensuel') {
          expirationDate.setMonth(expirationDate.getMonth() + 1);
        } else {
          expirationDate.setFullYear(expirationDate.getFullYear() + 1);
        }

        // Mettre  jour le statut Premium de l'utilisateur
        await updateDoc(doc(db, 'users', currentUser.uid), {
          isPremium: true,
          subscriptionEnd: expirationDate,
          subscriptionPlan: selectedPlan,
          updatedAt: serverTimestamp(),
        });

        // Mettre  jour la transaction
        await updateDoc(doc(db, 'transactions', transactionRef.id), {
          statut: 'success',
          paytechTransactionId: 'DEMO_' + Date.now(),
          dateExpiration: expirationDate,
          updatedAt: serverTimestamp(),
        });

        // Rediriger vers la page de succs
        navigate('/premium/success', { 
          state: { 
            plan: plan.nom, 
            montant: plan.prix,
            expiration: expirationDate.toLocaleDateString('fr-FR'),
          } 
        });
      }

    } catch (err) {
      console.error('Erreur paiement:', err);
      setError('Une erreur est survenue lors du paiement. Veuillez ressayer.');
    } finally {
      setLoading(false);
    }
  };

  /**
   * Toggle FAQ
   */
  const toggleFaq = (index: number) => {
    setOpenFaq(openFaq === index ? null : index);
  };

  /**
   * Formater le prix en FCFA
   */
  const formatPrice = (price: number): string => {
    return price.toLocaleString('fr-FR') + ' FCFA';
  };

  // ==================== RENDU SI DJ PREMIUM ====================
  if (isAlreadyPremium()) {
    const endDate = currentUser?.subscriptionEnd instanceof Date 
      ? currentUser.subscriptionEnd 
      : new Date(currentUser?.subscriptionEnd || Date.now());

    return (
      <div className="premium-page">
        <div className="container">
          <div className="premium-already">
            <span className="premium-already__icon"></span>
            <h1>Vous tes dj Premium !</h1>
            <p>
              Votre abonnement est actif jusqu'au{' '}
              <strong>{endDate.toLocaleDateString('fr-FR')}</strong>
            </p>
            <div className="premium-already__actions">
              <Link to="/disciplines" className="btn btn--primary">
                Accder aux cours
              </Link>
              <Link to="/dashboard" className="btn btn--outline">
                Mon tableau de bord
              </Link>
            </div>
          </div>
        </div>
      </div>
    );
  }

  // ==================== RENDU PRINCIPAL ====================
  return (
    <div className="premium-page">
      {/* ========== HERO SECTION ========== */}
      <section className="premium-hero">
        <div className="container">
          <span className="premium-hero__badge"> Premium</span>
          <h1 className="premium-hero__title">
            Dbloquez tout le potentiel de <span>PedaClic</span>
          </h1>
          <p className="premium-hero__subtitle">
            Accdez  tous les cours, exercices corrigs, vidos et quiz 
            de la 6me  la Terminale. Russissez vos examens !
          </p>
        </div>
      </section>

      {/* ========== SECTION TARIFS ========== */}
      <section className="premium-pricing">
        <div className="container">
          <h2 className="section-title">Choisissez votre formule</h2>
          
          <div className="pricing-cards">
            {PLANS.map((plan) => (
              <div 
                key={plan.id}
                className={`pricing-card ${selectedPlan === plan.id ? 'pricing-card--selected' : ''} ${plan.popular ? 'pricing-card--popular' : ''}`}
                onClick={() => setSelectedPlan(plan.id)}
              >
                {/* Badge populaire */}
                {plan.popular && (
                  <span className="pricing-card__badge">Le plus populaire</span>
                )}

                {/* En-tte */}
                <div className="pricing-card__header">
                  <h3 className="pricing-card__name">{plan.nom}</h3>
                  <p className="pricing-card__duration">{plan.duree}</p>
                </div>

                {/* Prix */}
                <div className="pricing-card__price">
                  {plan.prixOriginal && (
                    <span className="pricing-card__original">
                      {formatPrice(plan.prixOriginal)}
                    </span>
                  )}
                  <span className="pricing-card__amount">
                    {formatPrice(plan.prix)}
                  </span>
                  {plan.id === 'mensuel' && (
                    <span className="pricing-card__period">/mois</span>
                  )}
                </div>

                {/* conomie */}
                {plan.economie && (
                  <p className="pricing-card__savings">
                    conomisez {plan.economie}
                  </p>
                )}

                {/* Radio button */}
                <div className="pricing-card__radio">
                  <span className={`radio-dot ${selectedPlan === plan.id ? 'radio-dot--active' : ''}`}></span>
                </div>
              </div>
            ))}
          </div>

          {/* Bouton paiement */}
          <div className="pricing-cta">
            {error && (
              <div className="pricing-error">
                <span></span> {error}
              </div>
            )}
            
            {!currentUser ? (
              <div className="pricing-login">
                <p>Connectez-vous pour souscrire  Premium</p>
                <Link 
                  to="/connexion" 
                  state={{ from: '/premium' }}
                  className="btn btn--primary btn--lg"
                >
                  Se connecter
                </Link>
                <p className="pricing-login__register">
                  Pas encore inscrit ?{' '}
                  <Link to="/inscription" state={{ from: '/premium' }}>
                    Crer un compte
                  </Link>
                </p>
              </div>
            ) : (
              <button 
                className="btn btn--premium btn--lg"
                onClick={handlePayment}
                disabled={loading}
              >
                {loading ? (
                  <>
                    <span className="spinner-sm"></span>
                    Traitement en cours...
                  </>
                ) : (
                  <>
                    Payer {formatPrice(PLANS.find(p => p.id === selectedPlan)?.prix || 0)}
                  </>
                )}
              </button>
            )}

            {/* Moyens de paiement */}
            <div className="payment-methods">
              <p>Paiement scuris par</p>
              <div className="payment-methods__logos">
                <span className="payment-logo"> Orange Money</span>
                <span className="payment-logo"> Wave</span>
                <span className="payment-logo"> Free Money</span>
                <span className="payment-logo"> Carte bancaire</span>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* ========== SECTION AVANTAGES ========== */}
      <section className="premium-features">
        <div className="container">
          <h2 className="section-title">Tout ce que vous obtenez</h2>
          <p className="section-subtitle">
            Un accs complet  toutes les ressources pdagogiques
          </p>

          <div className="features-grid">
            {AVANTAGES.map((avantage, index) => (
              <div key={index} className="feature-card">
                <span className="feature-card__icon">{avantage.icone}</span>
                <h3 className="feature-card__title">{avantage.titre}</h3>
                <p className="feature-card__description">{avantage.description}</p>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* ========== SECTION COMPARAISON ========== */}
      <section className="premium-comparison">
        <div className="container">
          <h2 className="section-title">Gratuit vs Premium</h2>

          <div className="comparison-table">
            <div className="comparison-header">
              <div className="comparison-feature">Fonctionnalits</div>
              <div className="comparison-free">Gratuit</div>
              <div className="comparison-premium">Premium</div>
            </div>

            {[
              { feature: 'Cours de base', free: true, premium: true },
              { feature: 'Exercices avec corrections', free: false, premium: true },
              { feature: 'Vidos explicatives', free: false, premium: true },
              { feature: 'Quiz interactifs', free: false, premium: true },
              { feature: 'Suivi de progression', free: false, premium: true },
              { feature: 'Tlchargement de documents', free: false, premium: true },
              { feature: 'Support prioritaire', free: false, premium: true },
              { feature: 'Accs mobile optimis', free: true, premium: true },
            ].map((row, index) => (
              <div key={index} className="comparison-row">
                <div className="comparison-feature">{row.feature}</div>
                <div className="comparison-free">
                  {row.free ? (
                    <span className="check"></span>
                  ) : (
                    <span className="cross"></span>
                  )}
                </div>
                <div className="comparison-premium">
                  <span className="check check--premium"></span>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* ========== SECTION TMOIGNAGES ========== */}
      <section className="premium-testimonials">
        <div className="container">
          <h2 className="section-title">Ils ont choisi Premium</h2>

          <div className="testimonials-grid">
            <div className="testimonial-card">
              <div className="testimonial-card__stars">
                {''.repeat(5)}
              </div>
              <p className="testimonial-card__text">
                "Grce  PedaClic Premium, j'ai eu 16/20 au BFEM. Les exercices corrigs m'ont beaucoup aid !"
              </p>
              <div className="testimonial-card__author">
                <div className="testimonial-card__avatar">AN</div>
                <div>
                  <p className="testimonial-card__name">Aminata Ndiaye</p>
                  <p className="testimonial-card__info">3me - Dakar</p>
                </div>
              </div>
            </div>

            <div className="testimonial-card">
              <div className="testimonial-card__stars">
                {''.repeat(5)}
              </div>
              <p className="testimonial-card__text">
                "Les vidos sont trs bien expliques. Je comprends mieux les maths maintenant. Merci PedaClic !"
              </p>
              <div className="testimonial-card__author">
                <div className="testimonial-card__avatar">IF</div>
                <div>
                  <p className="testimonial-card__name">Ibrahima Fall</p>
                  <p className="testimonial-card__info">Terminale S - This</p>
                </div>
              </div>
            </div>

            <div className="testimonial-card">
              <div className="testimonial-card__stars">
                {''.repeat(5)}
              </div>
              <p className="testimonial-card__text">
                "20 000 FCFA pour l'anne, c'est vraiment abordable. Mon fils a accs  tout ce dont il a besoin."
              </p>
              <div className="testimonial-card__author">
                <div className="testimonial-card__avatar">FD</div>
                <div>
                  <p className="testimonial-card__name">Fatou Diop</p>
                  <p className="testimonial-card__info">Parent d'lve - Saint-Louis</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* ========== SECTION FAQ ========== */}
      <section className="premium-faq">
        <div className="container">
          <h2 className="section-title">Questions frquentes</h2>

          <div className="faq-list">
            {FAQ.map((item, index) => (
              <div 
                key={index}
                className={`faq-item ${openFaq === index ? 'faq-item--open' : ''}`}
              >
                <button 
                  className="faq-item__question"
                  onClick={() => toggleFaq(index)}
                  aria-expanded={openFaq === index}
                >
                  <span>{item.question}</span>
                  <span className="faq-item__icon">
                    {openFaq === index ? '' : '+'}
                  </span>
                </button>
                {openFaq === index && (
                  <div className="faq-item__answer">
                    <p>{item.reponse}</p>
                  </div>
                )}
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* ========== CTA FINALE ========== */}
      <section className="premium-cta-final">
        <div className="container">
          <h2>Prt  russir vos examens ?</h2>
          <p>Rejoignez des milliers d'lves sngalais qui ont choisi PedaClic Premium</p>
          <button 
            className="btn btn--white btn--lg"
            onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })}
          >
            Commencer maintenant
          </button>
        </div>
      </section>
    </div>
  );
};

export default PremiumPage;
